import React from 'react';
import { View, Dimensions, TouchableHighlight, StyleSheet, Text } from 'react-native';
let MainWidth = Dimensions.get('window').width;
export default function ActionSheetItem(props) {
  const {
    onPress = () => {},
    activeOpacity = 1,
    underlayColor = '#f1f1f1',
    containerStyle,
    textStyle,
    children
  } = props;
  return <TouchableHighlight activeOpacity={activeOpacity} underlayColor={underlayColor} onPress={onPress}>
      <View style={StyleSheet.flatten([styles.actionSheetItem, containerStyle])}>
        <Text style={StyleSheet.flatten([styles.actionSheetItemText, textStyle])}>{children}</Text>
      </View>
    </TouchableHighlight>;
}
const styles = StyleSheet.create({
  actionSheetItem: {
    width: MainWidth,
    height: 50,
    justifyContent: 'center',
    alignItems: 'center'
  },
  actionSheetItemText: {
    fontSize: 20,
    fontWeight: '400'
  }
});